// === 07 Widgets - 02 Breadcrumb Menu ======================================== ***
#content-main #breadcrumb-box {
  padding           : 0.125em 0 0.25em 1.0em;
  background-color  : $c-base-bg;
  color             : $c-gray-border;
  text-transform    : lowercase;
  font-variant      : small-caps;
  a {
    font-family     : $f-nav;
    border          : 0;
    font-size: 1.125em;
  }
  span.position {
    font-weight: bold;
    font-size: 1.125em;
   }
  a:hover, a:focus {
    @include set-blended-border($c-link-text, bottom, 0.85, 1px, solid, $c-base-bg);
  }
  a:visited:hover, a:visited:focus {
    @include set-blended-border($c-visited-text, bottom, 0.85, 1px, solid, $c-base-bg);
  }
}

// Media rules for small screens ---------------------------------------------- ***
@media (max-width: $break-1024-desktop) {
  #content-main #breadcrumb-box {
    @include set-blended-border($c-banner-bg, top, 0.25, 1px, solid, $c-base-bg);
    @include set-blended-border($c-banner-bg, bottom, 0.25, 1px, solid, $c-base-bg)
  }
  #breadcrumb-box {
    @include set-blended-border($c-banner-bg, top, 0.25, 1px, solid, $c-base-bg)
  }
}
