// === 06 Parts- 05 Utiity Classes ============================================ ***
.center {
  text-align        : center;
  margin-left       : auto;
  margin-right      : auto;
}
.fleft {
  float             : left;
  margin-right      : 1.0em;
}
.fright {
  float             : right;
  margin-left       : 1.0em;
 }
.clear-fix { clear  : both; }
.clear-left { clear : left; }
.clear-right { clear: right; }
.fixed-top {
  position          : fixed;
  top               : 0;
  left              : 0;
  width             : 100%;
}
.fixed-bottom {
  position          : fixed;
  bottom            : 0;
  left              : 0;
  width             : 100%;
}
                    // Hidden element rules                                     ***
.mobile-only, .hide { display: none; }
.hidden {
  width             : 0;
  height            : 0;
  overflow          : hidden;
}
.toggleHide { display: none !important; }
.toggleShow { display: block !important; }

                    // no-script tweak for jump link scroll height              ***
div.anchor,
div.anchor:focus { outline: none; }
.anchor:before {
  display           : block;
  position          : relative;
  width             : 0;
  height            : 2.5em;
  margin-top        : -2.5em;
  content           : '';
}
